import React from 'react';
import Box from '@mui/material/Box';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import { connect } from 'react-redux'
import * as actionTypes from '../store/actions';
import { myData } from '../actions';
import Rating from '@mui/material/Rating';
import Link from '@mui/material/Link';

function BarList({ activity }) {
  return (
    <Box sx={{ width: '100%' }}>
      <Stack spacing={2}>
      <Typography sx={{ mt: 2 }}>{Object.keys(myData[activity]).length} bars that mention {activity}</Typography>
      {Object.keys(myData[activity]).map((item, pos) => {
        return (
          <Box key={pos}>
          <Typography key={"typ" + pos} variant="h5">{myData[activity][item]['name']}</Typography>
          <Typography key={"typ3" + pos}>{myData[activity][item]['location']}</Typography>
          <Box sx={{
            display: 'flex',
            alignItems: 'center',
            flexWrap: 'wrap',
            mb: '3px'
          }}>
          <Rating inline key={"rat" + pos} name="stars" value={myData[activity][item]['stars']} precision={0.5} getLabelText={function(value) { return `${myData[activity][item]['numReviews']}` }} readOnly />
          <Typography inline key={"typ2" + pos} sx={{ ml: 1 }}>(<Link href={"https://www.yelp.com/biz/" + item} target="_blank" rel="noreferrer">{myData[activity][item]['numReviews']} reviews</Link>)</Typography>
          </Box>
          <Typography>{myData[activity][item]['numReviewsAct']} reviews mention {activity}</Typography>
          <Box>
          {myData[activity][item]['snippets'] ? myData[activity][item]['snippets'].map((d,pos2) => (
            <Typography variant="body2" key={"snip" + pos + pos2}>{'"...' + d + '..."'}</Typography>
          )) : ''}
          </Box>
          </Box>
        )
      })}
      </Stack>
    </Box>
  )
}

const mapStateToProps = (state) => ({
  activity: state.activity
})

export default connect(mapStateToProps)(BarList);