import React from 'react';
import Box from '@mui/material/Box';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import { myData } from '../actions';
import { connect } from 'react-redux'
import * as actionTypes from '../store/actions';

const ActivitySelect = ({ setActivity, activity }) => {
  const handleChange = (event) => {
    setActivity(event.target.value);
  }

  return (
    <Box sx={{ maxWidth: 200 }}>
      <FormControl fullWidth>
        <Select
          id="activity"
          onChange={handleChange}
          value={activity}
        >
          {Object.keys(myData).map((item, pos) => {
            return (
              <MenuItem key={pos} value={item}>
                {item}
              </MenuItem>
            )
          })}
        </Select>
      </FormControl>
    </Box>
  )
}

const mapStateToProps = (state) => ({
  activity: state.activity
})

const mapDispatchToProps = dispatch => ({
  setActivity: (activity) => dispatch(actionTypes.setActivity(activity)),
})

export default connect(mapStateToProps, mapDispatchToProps)(ActivitySelect)