import * as actionTypes from '../actions/actionTypes'

const initialState = {
    activity: "live music"
}

const activityFilter = (state = initialState, action) => {
    switch (action.type) {
        case actionTypes.SET_ACTIVITY:
            return {
                ...state,
                activity: action.activity
             }
        default:
            return state;
    }
}

export default activityFilter